import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { useAppSelector, useAppDispatch } from "../../hooks";
import { setInit } from "../../slice";
import { KeepLogin, LoginMsg } from "../../utlis/verifySteps";
import { Container, LoginContainer, ButtonContainer, Span } from "./style";
import { ButtonComponent } from "../../components/button";
import { Input } from "../../components/input";
import { Tooltip } from "../../components/toolTip";
import { Load } from "../../components/load";
import { Notification } from "@douyinfe/semi-ui";
export default function Login() {
  const [password, setPassword] = useState("");
  // loading,loading
  // 
  const [lastLoading, setLastLoading] = useState(false);

  const error = useAppSelector((state) => state.identity.error);
  // init
  // ,
  const init = useAppSelector((state) => state.identity.init);
  // loading,
  const loading = useAppSelector((state) => state.identity.loading);
  // status
  const status = useAppSelector((state) => state.identity.status);
  // 
  const config = useAppSelector((state) => state.config.config);
  // dispatchstore
  const dispatch = useAppDispatch();
  const nav = useNavigate();
  useEffect(() => {
    // 
    status && nav("home");
  }, [status, nav]);

  useEffect(() => {
    // initfalse,
    // !init,
    if (Object.keys(config).length !== 0 && !init) {
      // token
      dispatch(KeepLogin(config));
      // init
      dispatch(setInit(true));
    } else {
      // loading
      // submit,,loading
      // loading,loading
      // loadingtrue,loading
      if (lastLoading && !status) {
        Notification.error({
          title: "",
          content: error,
          duration: 3,
          theme: "light",
        });
        // 
        dispatch(setInit(true));
      }
    }

    // ,loading
    // loadingloading
    setLastLoading(loading);
  }, [loading, config]);
  return (
    <Container>
      <Load open={loading} window={true}>
        <LoginContainer
          onSubmit={(e) => {
            e.preventDefault();
            dispatch(LoginMsg({ config: config, password: password }));
          }}
        >
          <Span style={{ margin: "5px 0" }}>
            Secret
            <Tooltip descripton="" width="5rem">
              <Span>
                <i className="bi bi-lock-fill" />
              </Span>
            </Tooltip>
          </Span>
          <Input
            style={{ fontSize: "1.2rem", height: "1.5rem" }}
            width="95%"
            type="password"
            onChange={(e) => setPassword(e)}
          />
          <ButtonContainer>
            <ButtonComponent style={{ margin: "5px 5px" }} type="submit">
              
            </ButtonComponent>
            <ButtonComponent
              confirm={false}
              style={{
                margin: "5px 5px",
              }}
              type="button"
              onClick={() => nav("/reset")}
            >
              
            </ButtonComponent>
          </ButtonContainer>
        </LoginContainer>
      </Load>
    </Container>
  );
}
